import React, { Component } from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Checkbox, Upload, Icon } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
const { TextArea } = Input;
import FinancingItem from '../../../components/FormItem/financingItem.jsx'
import InvestItem from '../../../components/FormItem/investItem.jsx'
import DeadlineItem from '../../../components/FormItem/deadlineItem.jsx'
import MortgageItem from '../../../components/FormItem/mortgageItem.jsx'

export class DebtsForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            "assetsType": {
                "debts": [],
                "stock": [],
                "purchase": [],
                "assetsAcquisition": []
            },
            "industryList": [],
            "provinceList": {},
            "key": "",      //编号
            "title": "",    //标题
            "category": "",         //类别
            "stockType": "",  //参股/控股
            "type": [],        //投资类型
            "industry": [],    //投资行业
            "region": [],     //投资地区
            "financingProfits": {
                "isNeed": "0",
                "number": ""
            },     //融资方利润
            "invest": {     //单笔投资区间
                "min": "",
                "max": ""
            },
            "deadline": {   //期限
                "min": "",
                "max": ""
            },
            "minRate": "",     //最低接受利率
            "mortgage": {       //抵押物
                "isNeed": "0",    //是否有抵押物
                "type": [],  //抵押物类型
                "rate": {       //折扣率
                    "min": "",
                    "max": ""
                }
            },
            "guarantee": {
                "isNeed": "0",    //是否必须担保
                "method": ""   //担保方式
            },
            "estimateBenefits": {
                "times": "",
                "type": ""
            },
            "needCost": "0",        //前期费用
            "detail": "",   //详情
            "accessory": {        //附件
                "name": "",
                "link": ""
            },
            "receivedItemsNum": "", //收到的投递项目
            'fileList': []
        };
        ['publish', 'save', 'resetForm', 'mortgageChange', 'guaranteeChange', 'uploadChange'].forEach(m => this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        ajax({
            url: "/fund/publish/getInfoDetail",
            data: { id: that.props.infoID ? that.props.infoID : "" },
            callback: function (data) {
                let newData = data;
                that.setState(Object.assign(that.state, newData));
                that.setState({
                    fileList: [{
                        uid: "random",
                        name: newData.accessory.name,
                        url: newData.accessory.link,
                        status: "done",
                        response: 'Server Error 500'
                    }]
                })
            }
        })
    }

    financingProfitsCheck(rule, value, callback) {
        if ((value.isNeed == "0") || (value.isNeed > 0 && value.number)) {
            callback();
            return;
        }
        callback('请输入去年利润金额!');
    }

    investCheck(rule, value, callback) {
        if (!value.min || !value.max) {
            callback('请输入单笔投资区间!');
        } else {
            if (value.max < value.min) {
                callback("投资上限必须大于下限!")
            }
            callback();
            return;
        }
    }

    deadlineCheck(rule, value, callback) {
        if (!value.min || !value.max) {
            callback('请输入期限!');
        } else {
            if (value.max < value.min) {
                callback("期限上限必须大于下限!")
            }
            callback();
            return;
        }
    }

    mortgageChange(e) {
        this.state.mortgage.isNeed = e.target.value;
        this.setState({
            mortgage: this.state.mortgage
        });
    }

    guaranteeChange(e) {
        this.state.guarantee.isNeed = e.target.value;
        this.setState({
            guarantee: this.state.guarantee
        });
    }

    publish(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                ajax({
                    url: "/fund/publish/doPublish",
                    data: Object.assign(values, { id: this.props.infoID }, { accessory: { name: this.state.accessory.name, link: this.state.accessory.link } }),
                    callback: function (data) {
                        message.success("发布成功!", 1, function () {
                            hashHistory.push("/fund/space/published")
                        });
                    }
                });
            }
        })
    }

    save(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                ajax({
                    url: "/fund/publish/saveDraft",
                    data: Object.assign(values, { id: this.props.infoID }, { accessory: { name: this.state.accessory.name, link: this.state.accessory.link } }),
                    callback: function (data) {
                        message.success("保存成功!", 1, function () {
                            hashHistory.push("/fund/space/draft")
                        });
                    }
                });
            }
        })
    }

    resetForm() {
        this.props.form.resetFields();
    }

    uploadChange(info) {
        let that = this;
        let file = info.file;
        let fileList = info.fileList;
        this.setState({ loading: true });
        if (fileList.length > 0) {
            if (fileList.length > 1) {
                fileList = [file];
            }
            if (fileList[0].status == "error") {
                fileList = [];
                message.error("上传图片失败!");
            }
            if (fileList[0].status == "done") {
                fileList[0].url = `${fileList[0].response.url}`;
                this.setState({
                    accessory: {
                        name: fileList[0].name,
                        link: fileList[0].url
                    }
                })
            }
        } else {
            this.setState({
                accessory: {
                    name: "",
                    link: ""
                }
            })
        }

        this.setState({ fileList, loading: false });
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 14 }
        };
        var regionOps = [];
        for(var key in this.state.provinceList) {
            regionOps.push({
                key,
                value: this.state.provinceList[key]
            });
        }

        return (
            <Form>
                <FormItem
                    required
                    {...formItemLayout}
                    label="资金标题"
                    hasFeedback={!this.state.disabled}
                >
                    {getFieldDecorator("title", {
                        rules: [{ required: true, message: "请输入标题!" }],
                        initialValue: this.state.title
                    })(
                        <Input placeholder="请输入资金标题" />
                        )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="资金类别"
                >
                    {getFieldDecorator("category", {
                        initialValue: "1"
                    })(
                        <Select disabled>
                            <Option value="1">债权</Option>
                            <Option value="2">股权</Option>
                            <Option value="3">资金收购</Option>
                        </Select>
                        )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="投资类型"
                    hasFeedback
                >
                {getFieldDecorator('type', {
                    initialValue: this.state.type
                })(
                    <Select
                        className="multiSelect"
                        placeholder="请选择类型"
                        mode="multiple"
                    >
                        {this.state.assetsType.debts.map(item =>
                            <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                        )}
                    </Select>
                )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="投资行业"
                    hasFeedback
                >
                    {getFieldDecorator('industry', {
                        initialValue: this.state.industry
                    })(
                        <Select
                            className="multiSelect"
                            placeholder="请选择行业"
                            mode="multiple"
                        >
                            {this.state.industryList.map(item =>
                                <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                            )}
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="投资地区"
                    hasFeedback
                >
                    {getFieldDecorator('region', {
                        initialValue: this.state.region
                    })(
                        <Select
                            className="multiSelect"
                            placeholder="请选择地区"
                            mode="multiple"
                        >
                            {regionOps.map(item =>
                                <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                            )}
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="融资方去年净利润"
                    hasFeedback
                >
                    {getFieldDecorator("financingProfits", {
                        rules: [{
                            validator: this.financingProfitsCheck
                        }],
                        initialValue: {
                            isNeed: this.state.financingProfits.isNeed,
                            number: this.state.financingProfits.number
                        }
                    })(<FinancingItem  />)}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="单笔投资区间"
                    hasFeedback
                >
                    {getFieldDecorator("invest", {
                        rules: [{
                            validator: this.investCheck
                        }],
                        initialValue: {
                            min: this.state.invest.min,
                            max: this.state.invest.max
                        }
                    })(<InvestItem />)}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="期限"
                    hasFeedback
                >
                    {getFieldDecorator("deadline", {
                        rules: [{
                            validator: this.deadlineCheck
                        }],
                        initialValue: {
                            min: this.state.deadline.min,
                            max: this.state.deadline.max
                        }
                    })(<DeadlineItem />)}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="最低接受利率"
                    hasFeedback
                >
                    {getFieldDecorator("minRate", {
                        rules: [{
                            required: true,
                            message: "请设置最低接受利率!"
                        }],
                        initialValue: this.state.minRate
                    })(
                        <Input placeholder="请输入最低接受利率" />
                        )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="是否必须有抵押物"
                    hasFeedback
                >
                    {getFieldDecorator("mortgage.isNeed", {
                        rules: [{
                            required: true,
                            message: "请选择是否有抵押物!"
                        }],
                        initialValue: this.state.mortgage.isNeed
                    })(
                        <RadioGroup onChange={this.mortgageChange}>
                            <Radio value="1">是</Radio>
                            <Radio value="0">否</Radio>
                        </RadioGroup>
                        )}
                </FormItem>
                {
                    this.state.mortgage.isNeed == "1" ?
                        <FormItem
                            {...formItemLayout}
                            label="抵/质押物类型"
                            hasFeedback
                        >
                            {getFieldDecorator("mortgage.type", {
                                initialValue: this.state.mortgage.type
                            })(
                                <CheckboxGroup disabled={this.state.disabled}>
                                    <Checkbox value="1">固定资产</Checkbox>
                                    <Checkbox value="2">有价证券</Checkbox>
                                    <Checkbox value="3">其他</Checkbox>
                                </CheckboxGroup>
                                )}
                        </FormItem> : ""
                }
                {
                    this.state.mortgage.isNeed == "1" ?
                        <FormItem
                            {...formItemLayout}
                            label="抵/质押物折扣率"
                        >
                            {getFieldDecorator("mortgage", {
                                initialValue: {
                                    min: this.state.mortgage.rate.min,
                                    max: this.state.mortgage.rate.max
                                }
                            })(<MortgageItem />)}
                        </FormItem> : ""
                }
                <FormItem
                    required
                    {...formItemLayout}
                    label="是否必须要担保"
                    hasFeedback
                >
                    {getFieldDecorator("guarantee.isNeed", {
                        rules: [{
                            required: true,
                            message: "请设置是否需要担保!"
                        }],
                        initialValue: this.state.guarantee.isNeed
                    })(
                        <RadioGroup onChange={this.guaranteeChange}>
                            <Radio value="1">是</Radio>
                            <Radio value="0">否</Radio>
                        </RadioGroup>
                        )}
                </FormItem>
                {
                    this.state.guarantee.isNeed == "1" ?
                        <FormItem
                            {...formItemLayout}
                            label="担保方式"
                        >
                            {getFieldDecorator("guarantee.method", {
                                initialValue: this.state.guarantee.method
                            })(
                                <Input placeholder="请输入担保方式" />
                                )}
                        </FormItem> : ""
                }
                <FormItem
                    {...formItemLayout}
                    label="前期费用"
                >
                    {getFieldDecorator("needCost", {
                        initialValue: this.state.needCost
                    })(
                        <RadioGroup>
                            <Radio value="1">有(评估/担保等)</Radio>
                            <Radio value="0">没有</Radio>
                        </RadioGroup>
                        )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="资金详情"
                    hasFeedback
                >
                    {getFieldDecorator("detail", {
                        rules: [{ required: true, message: "请输入资金详情!" }],
                        initialValue: this.state.detail
                    })(
                        <TextArea autosize={{ minRows: 8 }} />
                        )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="附件"
                >
                    <Upload
                        name="file"
                        action="/v1/upload"
                        fileList={this.state.fileList}
                        onChange={this.uploadChange}
                    >
                        <Button>
                            <Icon type="upload" />上传文件
                        </Button>
                    </Upload>
                </FormItem>
                <FormItem wrapperCol={{ span: 16, offset: 3 }}>
                    <Button type="primary" onClick={this.publish} style={{ "marginRight": "20px" }}>立即发布</Button>
                    <Button type="danger" onClick={this.resetForm} style={{ "marginRight": "20px" }}>重置</Button>
                    <Button onClick={this.save}>保存</Button>
                </FormItem>
            </Form>
        )
    }
}

const WrappedDebtsForm = Form.create({})(DebtsForm);

module.exports = WrappedDebtsForm;


